body {
  background: linear-gradient(90deg, #454545, #212121);
}

.container {
  max-width: 1366px;
  margin: 0 auto;
}

header h1 {
  padding-left: 23px;
  color: #fff;
}
header p {
  color: #767676;
}
header .wrap {
  display: flex;
  gap: 20px;
  align-items: baseline;
}

.promo {
  margin-top: 30px;
}
.promo__list {
  padding: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(auto-fill, 419px);
  gap: 10px;
}
.promo__item {
  position: relative;
  overflow: hidden;
  border-radius: 30px;
}
.promo__item:hover .promo__overlay {
  visibility: visible;
  opacity: 1;
}
.promo__overlay {
  background: linear-gradient(90deg, rgba(53, 53, 53, 0.8), rgba(33, 33, 33, 0.8));
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  visibility: hidden;
  opacity: 0;
  transition: all 0.4s ease-in-out;
}
.promo__overlay h2 {
  color: #fff;
}
.promo__overlay h3 {
  color: #8f8f8f;
}
.promo__overlay .loves {
  display: none;
}
.promo__info {
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  width: 300px;
}

.project__wrap {
  display: grid;
  grid-template-columns: 1fr 1fr;
}